<template>
    <div class="container-fluid page-content">
        <div class="row">
            <div class="col-md-8 offset-md-2 post-title">
                <h1>{{ post[0].title }}</h1>
                <p>
                    on
                    <span class="date">{{ post[0].created_at | formatDate }}</span> in
                    <router-link :to="{ name: 'Tag', params: { tag: post[0].tag.url } }">{{ post[0].tag.title }}</router-link>
                </p>
            </div>

            <div class="col-md-8 offset-md-2 text-center" v-if="post[0].image !== null">
                <img class="img-fluid rounded" :src="'/uploads/images/' + post[0].image.file_name" />
            </div>

            <div v-bind:class="{ 'col-md-6 offset-md-3': post[0].image !== null, 'col-md-8 offset-md-2': post[0].image == null }">
                <div class="card" v-bind:class="{ 'post-single': post[0].image !== null, 'post-no-image': post[0].image == null }">
                    <div class="card-body">
                        <p class="card-text" v-html="markdown(post[0].content)"></p>
                    </div>
                </div>
            </div>

            <div class="col-md-8 offset-md-2 about-author">
                <div class="media">
                    <img class="mr-3 rounded" :src="'/uploads/profile/' + post[0].created_by.avatar" height="64" width="64" />
                    <div class="media-body">
                        <h5 class="mt-0"><router-link :to="{ name: 'User', params: { 'user': post[0].created_by._id } }">{{ post[0].created_by.username }}</router-link></h5>
                        {{ post[0].created_by.about }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import showdown from 'showdown'

    export default {
        dependencies: 'api',
        name: 'Post',
        created() {
            this.getPost();
        },
        watch: {
            '$route': ['getPost']
        },
        methods: {
            getPost() {
                axios.get(this.api + '/blog/url/' + this.$route.params.post)
                    .then((res) => {
                        //console.log(res.data);
                        this.post = res.data;
                        //console.log(res.data[0].tag);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            },
            markdown(value) {
                var converter = new showdown.Converter({
                    simpleLineBreaks: true
                });
                return converter.makeHtml(value);
            }
        },
        data() {
            return {
                post: {}
            }
        }
    }
</script>


