<template>
  <div id="app" class="row no-gutters wrapper">
    <div class="col-md-2 side-nav">
      <div class="container toggle-nav">
        <span class="logo btn">{{ site.name }}</span>
        <button v-b-toggle="'nav-wrapper'" class="btn btn-default float-right">
          <span class="fa fa-bars"></span>
        </button>
      </div>
      <b-collapse class="container nav-wrapper" v-model="collapse" id="nav-wrapper">
        <h1 class="logo">{{ site.name }}</h1>
        <ul class="main-nav">
          <li>
            <router-link :to="{ name: 'Blog' }" active-class="current">Home</router-link>
          </li>
          <li v-for="page in pages">
            <router-link :to="{ name: 'Page', params: { page: page.url } }" active-class="current">{{ page.title }}</router-link>
          </li>
        </ul>
      </b-collapse>
    </div>

    <div class="col-md-10 main-content">
      <router-view/>

      <footer class="footer">
        powered by
        <a href="https://github.com/jtpox/ribbon" target="_blank">ribbon</a>
      </footer>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    dependencies: 'api',
    name: 'App',
    created() {
      //console.log(this.api);
      this.getPages();
      this.getSite();
    },
    watch: {
        '$route': ['getPages', 'getSite'],
    },
    methods: {
      getPages() {
        axios.get(this.api + '/pages')
        .then((res) => {
          //console.log(res.data);
          this.pages = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
      },
      getSite() {
        axios.get(this.api)
        .then((res) => {
          //console.log(res.data);
          this.site = res.data;
          //console.log(res.data.name);
        })
        .catch((err) => {
          console.log(err);
        });
      }
    },
    data() {
      return {
        pages: {},
        site: {},
        collapse: true
      }
    }
  }
</script>
<style src="./css/bootstrap.css"></style>


