<template>
    <div class="container-fluid page-content">
        <div class="row">
            <div class="col-md-6 offset-md-3 text-center">
                <h1>{{ user.username }}</h1>
                <p class="lead">
                    {{ user.about }}
                </p>
            </div>

            <div class="col-md-6 offset-md-3" v-for="post in posts">
                <div class="card post">
                    <router-link :to="{ name: 'Post', params: { post: post.url } }" v-if="post.image !== null">
                        <img class="card-img-top" :src="'/uploads/images/' + post.image.file_name" />
                    </router-link>
                    <div class="card-body">
                        <small>
                            <router-link :to="{ name: 'Tag', params: { tag: post.tag.url } }">{{ post.tag.title }}</router-link>
                        </small>
                        <h5 class="card-title">
                            <router-link :to="{ name: 'Post', params: { post: post.url } }">{{ post.title }}</router-link>
                        </h5>
                        <p class="card-text" v-html="markdownLimit(post.content)"></p>
                        <p>
                            <small>
                                <router-link :to="{ name: 'User', params: { user: post.created_by._id } }">{{ post.created_by.username }}</router-link>
                                 on {{ post.created_at | formatDate }}
                            </small>
                        </p>
                    </div>
                </div>
            </div>

            <div class="col-md-6 offset-md-3 text-center" v-if="page < total_pages">
                <button type="button" class="btn btn-outline-danger" v-on:click="loadMore()">Older Posts</button>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import showdown from 'showdown'

    export default {
        dependencies: 'api',
        name: 'User',
        created() {
            this.getPosts();
        },
        watch: {
            '$route': ['getPosts']
        },
        methods: {
            getPosts() {
                axios.get(this.api + '/users/' + this.$route.params.user + '/page/' + this.page)
                    .then((res) => {
                        //console.log(res.data);
                        this.user = res.data.user;
                        this.posts = res.data.posts.docs;
                        this.total_pages = res.data.posts.pages;
                        //console.log(this.posts);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            },
            loadMore() {
                this.page = this.page + 1;
                    axios.get(this.api + '/users/' + this.$route.params.user + '/page/' + this.page)
                    .then((res) => {
                        for(var i = 0; i < res.data.docs.length; i++) {
                            this.posts.push(res.data.docs[i]);
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            },
            markdownLimit(value) {
                var converter = new showdown.Converter({
                    simpleLineBreaks: true
                });
                let val = value.split(' ').splice(0, 50).join(' ');
                return converter.makeHtml(val);
            }
        },
        data() {
            return {
                user: {},
                page: 1,
                total_pages: 0,
                posts: {}
            }
        }
    }
</script>


