<template>
    <div class="page-content">
        <img :src="'/uploads/images/' + page.details.image.file_name" v-if="page.details.image !== null" class="img-fluid page-image" />
        <div class="row no-gutters">
            <div class="col-md-8 offset-md-2">
                <div class="card" v-bind:class="{ 'post-single': page.details.image !== null, 'post-no-image': page.details.image == null }">
                    <div class="card-body text-center">
                        <h1 class="card-title">{{ page.details.title }}</h1>
                        <p class="card-text" v-html="markdown(page.details.description)"></p>
                    </div>
                </div>
            </div>
        </div>

        <div class="container-fluid">
            <div class="row">
                <div v-for="box in page.boxes" v-bind:class="'col-md-' + box.content_column">
                    <div class="card post">
                        <div class="card-body">
                            <h4 class="card-title">{{ box.title }}</h4>
                            <p class="card-text" v-html="markdown(box.content)"></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import showdown from 'showdown'

export default {
    name: 'Page',
    created() {
        this.fetchPage();
    },
    watch: {
        '$route': 'fetchPage'
    },
    methods: {
        fetchPage() {
            //console.log(this.$route.params.page);
            axios.get('http://localhost:8081/api/pages/url/' + this.$route.params.page)
            .then((res) => {
                //console.log(res.data.details);
                this.page = res.data;
                //console.log(this.page.details.title);
            })
            .catch((err) => {
                console.log(err);
            });
        },
        markdown(value) {
            var converter = new showdown.Converter({
                simpleLineBreaks: true
            });
            return converter.makeHtml(value);
        }
    },
    data() {
        return {
            page: {
                details: {},
                boxes: {}
            }
        }
    },
}
</script>


